import { AddAreaService } from './../../services/AddAreaService';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

import { Location } from '@angular/common';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { Router } from '@angular/router';
import { UserService } from '../../services/userService';


declare var jQuery: any;

@Component({
  selector: 'logistic',
  templateUrl: './list-logistic.component.html',
  styleUrls: [
    './list-logistic.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [UserService,AddAreaService, GetChemistDataService, MeasurementAndDosageSizesService]
})
export class ListLogisticComponent implements OnInit {

  public CompanyName: string = "";
  public ContactPersonName: string = "";

  public Address: string = "";
  public Email: string = "";
  public MobileNumber: number = 0;
  public City: number = 0;
  public Area: number = 0;

  public gotData:boolean = false;

  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  rowsFilter = [];
  tempFilter = [];
  public runner: boolean = false;



  @ViewChild(DatatableComponent) logisticList: DatatableComponent;


  constructor(public route: Router, public _AddAreaNameService: AddAreaService,
    public _userService:UserService,
    public _getChemistDataService: GetChemistDataService, public _measurementAndDosageSizesService: MeasurementAndDosageSizesService) {


  }



  ngOnInit() {

    this.getAllLogistics();
  }

  onSelect({ selected }) {

    console.log(selected);

  }

  onActivate(event) { }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.LogisticsName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.logisticList.offset = 0;
  }





  editLogistic(value) {

    var obj = this.rowsFilter.find(x => x.Id == value);

    localStorage.setItem('editLogistic', JSON.stringify(obj));

    this.route.navigateByUrl('/admin/logistic/edit-logistic?id='+value); 
  }


  updateChanges(editingId,value){
    
    for(var i = 0; i< this.rowsFilter.length ; i++){
      if(this.rowsFilter[i].Id == editingId){
        this.rowsFilter[i].IsBlocked = value;
      }
    }
  }


  inActivateThisUser(value){


  
    var EditingId = value;
    this.gotData = true;


    this._userService.activateInActivateUser(value).subscribe(a=>{
      console.log(a);

      if(a.code == 200){
         this.gotData = false;
         swal(a.message, "", "success");
         this.updateChanges(EditingId,a.data.IsBlocked);
      }
    },err=>{
      console.log(err);
      var obj = JSON.parse(err._body);
      swal(obj.message, "", "error");
      this.gotData = false;
    })




  }


  getAllLogistics() {
    this._measurementAndDosageSizesService.AllLogistics().subscribe(response => {
      console.log(response);
      this.rowsFilter = response.data;
      this.tempFilter = response.data;
    });
  }

}



