import { ListLogisticComponent } from './list-logistic.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { ListLogisticRoutingModule } from './list-logistic-routing.module';

@NgModule({
  imports: [
    CommonModule,
    ListLogisticRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule,
    HttpModule
  ],
  declarations: [ListLogisticComponent]
})
export class ListLogisticModule { }



