import { ListLogisticComponent } from './list-logistic.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    component: ListLogisticComponent,
    data: {
      title: 'List Logistic',
      status: false
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ListLogisticRoutingModule { }



